export default {
    functional: true,
    name: 'bk-icon',
    props: {
        type: {
            type: String,
            required: true
        },
        svg: {
            type: Boolean,
            default: false
        },
        size: {
            type: String
        }
    },
    render (h, context) {
        const { data, props, listeners } = context
        const classes = {
            'bk-icon': true
        }
        const style = {
            fontSize: props.size
        }
        if (data.class) {
            if (typeof data.class === 'string') {
                classes[data.class] = true
            } else if (Array.isArray(data.class)) {
                data.class.forEach(item => {
                    classes[item] = true
                })
            } else {
                Object.assign(classes, data.class)
            }
            delete data.class
        }
        if (data.staticClass) {
            classes[data.staticClass] = true
            delete data.staticClass
        }
        if (props.svg) {
            return h('svg', {
                ...data,
                style,
                class: classes,
                on: listeners
            }, [
                h('use', {
                    attrs: {
                        'xlink:href': `#icon-${props.type}`
                    }
                })
            ])
        }

        classes[`icon-${props.type}`] = true

        return h('i', {
            ...data,
            style,
            class: classes,
            on: listeners
        })
    }
}
