export default {
    methods: {
        getKeyDownSelected (target) {
            const ele = Array.prototype.find.call(target.children, node => node.nodeName === 'INPUT' && node.type === 'radio')
            return !ele.checked
        },
        handlerChange (event) {
            if (this.disabled) {
                return false
            }

            const selected = event.type === 'change' ? event.target.checked : this.getKeyDownSelected(event.target)
            const value = selected ? this.localTrueValue : this.falseValue
            this.$emit('input', value)
            this.$emit('change', value)

            if (this.parent) {
                this.parent.change({
                    value: value
                })
            }
        }
    }
}
