export default {
    name: 'tag-render',
    functional: true,
    props: {
        node: Object,
        displayKey: String,
        tpl: Function
    },
    render (h, ct) {
        const parentClass = 'tag'
        const textClass = 'text'
        if (ct.props.tpl) {
            return ct.props.tpl(ct.props.node, ct, h)
        }
        return (
            <div class={parentClass}>
                <span domPropsInnerHTML={ct.props.node[ct.props.displayKey]} class={textClass}></span>
            </div>
        )
    }
}
