<template>
    <div class="bk-slider" :class="[{ 'disable': disable }, extCls]">
        <div class="slide-bar" ref="slider">
            <div class="slide-inner" :style="barStyle"></div>
            <div v-if="range" class="slider-button" :style="`left: ${slideLeft}%`"
                tabindex="0"
                @keydown.left="changeSlideWidth('min', 'left')"
                @keydown.right="changeSlideWidth('min')"
                @mousedown="onPointerDown($event, 'min')"></div>
            <div v-if="showTip" class="slider-button" :style="`left: ${slideWidth}%`"
                tabindex="0"
                @keydown.left="changeSlideWidth('max', 'left')"
                @keydown.right="changeSlideWidth('max')"
                @mousedown="onPointerDown($event, 'max')" v-bk-tooltips.top="showCurrentValue"></div>
            <div v-else class="slider-button" :style="`left: ${slideWidth}%`"
                tabindex="0"
                @keydown.left="changeSlideWidth('max', 'left')"
                @keydown.right="changeSlideWidth('max')"
                @mousedown="onPointerDown($event, 'max')"></div>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue'
    import bkTooltips from '@/directives/tooltips'

    Vue.use(bkTooltips)

    export default {
        name: 'bk-slider',
        model: {
            prop: 'value',
            event: 'change'
        },
        props: {
            disable: {
                type: Boolean,
                default: false
            },
            range: {
                type: Boolean,
                default: false
            },
            value: {
                type: [Number],
                default: 0
            },
            maxValue: {
                type: [Number],
                default: 100
            },
            minValue: {
                type: [Number],
                default: 0
            },
            showTip: {
                type: Boolean,
                default: true
            },
            // 外部设置的 class name
            extCls: {
                type: String,
                default: ''
            }
        },
        data () {
            return {
                dragging: false,
                pointerDown: '',
                currentX: 0,
                startX: 0,
                slideWidth: this.comparison(this.minValue, this.value, this.maxValue), // 滑块滑动宽度
                startWidth: '', // 滑块起始宽度
                slideLeft: this.minValue,
                currentValue: [],
                step: 1
            }
        },
        computed: {
            barStyle () {
                const style = {}
                style.left = `${this.slideLeft}%`
                style.width = `${this.slideWidth - this.slideLeft}%`
                if (this.range) {
                    style.width = `${this.slideWidth - this.slideLeft}%`
                }
                return style
            },
            val (val) {
                val = Array.isArray(this.value) ? this.value : [this.value]
                if (val[0] !== this.currentValue[0] || val[1] !== this.currentValue[1]) {
                    this.currentValue = val
                }
                return val
            },
            showCurrentValue () {
                return Math.round(this.slideWidth).toString()
            }
        },
        watch: {
            slideWidth (val) {
                this.$emit('change', parseInt(val))
            },
            value (val) {
                this.slideWidth = val <= this.maxValue ? val >= this.minValue ? val : this.minValue : this.maxValue
            }
        },
        methods: {
            /** 键盘左右键切换 */
            changeSlideWidth (type, direction) {
                if (this.disable) {
                    return
                }
                this.pointerDown = type
                const distance = type === 'min' ? Math.floor(this.slideLeft) : Math.floor(this.slideWidth)
                const pos = direction === 'left' ? (distance - this.step) : (distance + this.step)
                if (type === 'min') {
                    this.changeButtonPosition(pos)
                } else {
                    this.slideWidth = pos
                }
            },
            comparison (min, value, max) {
                return value <= max ? value >= min ? value : min : max
            },

            /**
             * 添加监听事件
             */
            on (element, event, handler) {
                if (element && event && handler) {
                    element.addEventListener(event, handler, false)
                }
            },

            /**
             * 移除监听事件
             */
            off (element, event, handler) {
                if (element && event) {
                    element.removeEventListener(event, handler, false)
                }
            },

            /**
             * 鼠标按下
             */
            onPointerDown (event, type) {
                if (this.disable) {
                    return
                }
                event.preventDefault()
                this.pointerDown = type
                this.onPointerDragStart(event)
                this.startWidth = this.$refs.slider.querySelector('.slide-inner').clientWidth / this.$refs.slider.clientWidth * 100
                this.on(window, 'mousemove', this.onPointerDrag)
                this.on(window, 'mouseup', this.onPointerDragEnd)
            },

            /**
             * 拖拽开始
             */
            onPointerDragStart (event) {
                this.dragging = false
                this.startX = event.clientX - (this.slideLeft / 100 * this.$refs.slider.clientWidth) // 减去定位距离
                // this.startPos = (this[`${this.pointerDown}Position`] * this.valueRange / 100) + this.min
            },

            /**
             *  拖拉
             */
            onPointerDrag (event) {
                this.dragging = true
                this.currentX = event.clientX
                const sliderWidth = this.$refs.slider.clientWidth
                const diff = (this.currentX - this.startX) / sliderWidth * 100 // 移动距离
                this.changeButtonPosition(diff)
            },

            changeButtonPosition (pos) {
                if (this.pointerDown === 'min') {
                    this.slideLeft = pos
                    this.slideLeft = this.slideLeft > this.slideWidth
                        ? this.slideWidth
                        : this.slideLeft < this.minValue
                            ? this.minValue
                            : this.slideLeft
                } else {
                    const total = pos + this.startWidth
                    this.slideWidth = total > this.maxValue
                        ? this.maxValue
                        : total < this.minValue
                            ? this.minValue
                            : total
                }
            },

            /**
             *  拖拽结束
             */
            onPointerDragEnd () {
                if (this.dragging) {
                    this.dragging = false
                }
                this.pointerDown = ''
                this.off(window, 'mousemove', this.onPointerDrag)
                this.off(window, 'mouseup', this.onPointerDragEnd)
            }
        }
    }
</script>
<style>
    @import '../../ui/slider.css';
</style>
