import { initTime } from '@/utils/date'

export default {
    props: {
        showTime: {
            type: Boolean,
            default: false
        },
        format: {
            type: String,
            default: 'yyyy-MM-dd'
        },
        selectionMode: {
            type: String,
            default: 'date',
            validator (value) {
                if (['year', 'month', 'date', 'time'].indexOf(value) < 0) {
                    console.error(`selectionMode property is not valid: '${value}'`)
                    return false
                }
                return true
            }
        },
        disabledDate: {
            type: Function,
            default: () => false
        },
        value: {
            type: Array,
            default: () => [initTime(), initTime()]
        },
        timePickerOptions: {
            default: () => ({}),
            type: Object
        },
        startDate: {
            type: Date
        },
        pickerType: {
            type: String,
            require: true
        },
        focusedDate: {
            type: Date,
            required: true
        },
        // 结束时间是否允许“至今”
        upToNow: {
            type: Boolean,
            default: false
        }
    },
    computed: {
        isTime () {
            return this.currentView === 'time'
        }
    },
    methods: {
        handleToggleTime () {
            this.currentView = this.currentView === 'time' ? 'date' : 'time'
        }
    }
}
